*{
  padding: 0px;
  margin: 0px;
  outline: none;
  text-decoration: none;
  box-sizing: border-box;
  border: 0;
}

#container{
  background-color: #318085;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.App{
  background-color: #3f3160;
  width: 500px;
  height: 600px;
  border-radius: 7px; 
  
}

#to-do-form input{
  width: 350px;
  height: 50px;
  border-radius: 7px;
  margin: 25px 27px;
  padding: 0 10px;
  color: black;
  font-size: 23px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-weight: 600;
}

#to-do-form button{
  width: 70px;
  height: 30px;
  border-radius: 7px;
  color: black;
  font-size: 21px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-weight: 600;
}
