.list{
    color: white;
    font-size: 25px;
    text-align: left;
    background-color: tomato;
    border-radius: 7px;
    margin: 7px 30px;
    line-height: 40px;
    padding: 0px 7px;
    font-weight: 500;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    letter-spacing: 1px;
}

.list p{
    position: relative;
}

.list p span{
    position: absolute;
    right: 10px;
    cursor: pointer;
}

.list input{
    background-color: transparent;
    font-size: 25px;
    font-weight: 500;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    letter-spacing: 1px;
    color: white;
    /* border: 0; */
}

